import React, { useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, ButtonGroup, Button } from '@mui/material';
import { Calendar, momentLocalizer,  } from 'react-big-calendar';
import moment from 'moment';
import Timeline from 'react-calendar-timeline';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import 'react-calendar-timeline/lib/Timeline.css';
import 'react-calendar/dist/Calendar.css';
import 'moment/locale/vi'; //   

moment.locale('vi'); //    
const localizer = momentLocalizer(moment);

const messages = {
  allDay: 'C ngy',
  previous: 'Trc',
  next: 'Tip',
  today: 'Hm nay',
  month: 'Thng',
  week: 'Tun',
  day: 'Ngy',
  agenda: 'Lch trnh',
  date: 'Ngy',
  time: 'Thi gian',
  event: 'S kin',
  noEventsInRange: 'Khng c s kin no trong khong thi gian ny.',
  showMore: (total: number) => `+ thm ${total}`,
};

const formatDateTime = (dateTime: string) => {
  return moment(dateTime).format('HH:mm DD/MM/YYYY');
};

const WorkSchedule: React.FC = () => {
  const [data, setData] = useState<any>(null);
  const [view, setView] = useState<'list' | 'calendar' | 'timeline'>('list');

  useEffect(() => {
    fetch(process.env.REACT_APP_API_URL!)
      .then(response => response.json())
      .then(data => {
        if (!data.results) {
          console.error('No results found in the response');
          setData([]);
          return;
        }
        console.log('Fetched Data:', data);
        setData(data.results);
      })
      .catch(error => console.error('Error fetching data:', error));
  }, []);

  const getStatusClass = (color: string) => {
    switch (color) {
      case 'blue':
        return 'status-blue';
      case 'gray':
        return 'status-gray';
      case 'red':
        return 'status-red';
      case 'green':
        return 'status-green';
      default:
        return '';
    }
  };

  const renderListView = () => (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell> </TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data && data.length > 0 ? (
            data.map((row: any) => (
              <TableRow key={row.id}>
                <TableCell>{row.properties?.['']?.rollup?.array?.[0]?.select?.name ?? ''}</TableCell>
                <TableCell>{row.properties?.['']?.rollup?.array?.[0]?.title?.[0]?.text?.content ?? ''}</TableCell>
                <TableCell>{row.properties?.[' ']?.rollup?.array?.[0]?.phone_number ?? ''}</TableCell>
                <TableCell>{row.properties?.['']?.title?.[0]?.text?.content ?? ''}</TableCell>
                <TableCell>{row.properties?.['']?.rich_text?.[0]?.plain_text ?? ''}</TableCell>
                <TableCell>{row.properties?.['']?.rich_text?.[0]?.plain_text ?? ''}</TableCell>
                <TableCell>{formatDateTime(row.properties?.['']?.date?.start) ?? ''}</TableCell>
                <TableCell>{formatDateTime(row.properties?.['']?.date?.start) ?? ''}</TableCell>
                <TableCell>
                  <div className={`status-cell ${getStatusClass(row.properties?.['']?.status?.color)}`}>
                    <div className="status-dot"></div>
                    {row.properties?.['']?.status?.name ?? ''}
                  </div>
                </TableCell>
                <TableCell>
                  <div className={`status-cell ${getStatusClass(row.properties?.['']?.select?.color)}`}>
                    <div className="status-dot"></div>
                    {row.properties?.['']?.select?.name ?? ''}
                  </div>
                </TableCell>
              </TableRow>
            ))
          ) : (
            <TableRow>
              <TableCell colSpan={10} align="center"> </TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  );

  const renderCalendarView = () => {
    if (!data) return null;

    const events = data.map((event: any) => ({
      id: event.id,
      title: event.properties?.['']?.rich_text?.[0]?.plain_text,
      start: new Date(event.properties?.['']?.date?.start),
      end: new Date(event.properties?.['']?.date?.start)
    }));

    return (
      <Calendar
        localizer={localizer}
        events={events}
        views={['day', 'week', 'month']} // 'agenda'   
        startAccessor="start"
        endAccessor="end"
        style={{ height: 500 }}
        messages={messages} //   
      />
    );
  };

  const renderTimelineView = () => {
    if (!data) return null;

    const items = data.map((event: any, index: number) => ({
      id: index,
      group: 1,
      title: event.properties?.['']?.rich_text?.[0]?.plain_text,
      start_time: new Date(event.properties?.['']?.date?.start),
      end_time: new Date(event.properties?.['']?.date?.start)
    }));

    return (
      <div style={{ height: '500px' }}>
        <Timeline
          groups={[{ id: 1, title: '' }]}
          items={items}
          defaultTimeStart={new Date()}
          defaultTimeEnd={new Date(new Date().setDate(new Date().getDate() + 1))}
        />
      </div>
    );
  };

  return (
    <div>
      <ButtonGroup variant="contained">
        <Button onClick={() => setView('list')}></Button>
        <Button onClick={() => setView('calendar')}></Button>
        <Button onClick={() => setView('timeline')}></Button>
      </ButtonGroup>
      {view === 'list' && renderListView()}
      {view === 'calendar' && renderCalendarView()}
      {view === 'timeline' && renderTimelineView()}
    </div>
  );
};

export default WorkSchedule;
